.try{
    display: flex;
    justify-content: center;
    align-items: center;
}
.heading{
    font-size: 25px;
}
.notation{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.notation input{
    padding: 25px;
}
.input, .output{
    margin: 25px;
    border: 1px solid #999;
    border-radius: 10px;
    width: 400px;
    height: 255px;
    background-color: #b3b2b2;
    box-shadow: 2px 2px 10px rgb(137, 137, 137);
}
.input img, .output img{
    width: 400px;
    height: 255px;
    border-radius: 10px;
}

